<template>
  <div class="create-task-plan create-page-content">
    <top-navigation
      :previousPage="$t('BUC.taskPlan.bulkTask')"
      :currentPage="$t('BUC.taskPlan.createPageTitle')"
      :isOperationForm="isOperationForm"
    ></top-navigation>
    <div v-if="!createCompleted">
      <portal-header class="bulk-create-header" :projectObj="titleConfig" />
      <portal-form
        class="taskPlanForm"
        ref="taskPlanFormRef"
        :form-data="formData"
        :form-rules="formRules"
        :form-config-list="formConfigList"
        :labelPosition="'top'"
        :rowGutter="24"
        :smallAutoColumn="3"
        :bigAutoColumn="3"
        :needOperateBtn="false"
        :needBottomLine="false"
      >
        <template v-slot:basicInfoTitle>
          <div class="title">{{ $t('BUC.taskPlan.basicInfo') }}</div>
        </template>
        <template v-slot:upload>
          <el-form-item ref="fileContent" prop="fileContent" :label="$t('BUC.taskPlan.file')" class="upload-item">
            <el-button
              :disabled="!formData.bulkType"
              type="text"
              class="bulk-tsak-view-change"
              v-preventReClick
              @click="downloadTemplate"
            >
              {{ $t('BUC.taskPlan.downloadTemplate') }}
            </el-button>
            <portal-upload
              v-loading.lock="fullscreenLoading"
              :auto-upload="false"
              :status.sync="status"
              @removeFile="removeFile"
              :uploadFun="uploadFun"
              :fileWhiteList="[acceptFileFormat]"
              :disabled="!acceptFileFormat"
              @isUploadFun="isUploadFun"
              @isCancleFun="isCancleFun"
              @viewDetailDialog="viewDetailDialog"
              :totalItem="{
                totalCount: validResult.totalCount,
                uploadedItems: validResult.validCount,
                errorItems: validResult.errorCount,
              }"
              ref="uploadRef"
            />
          </el-form-item>
        </template>
      </portal-form>
      <portal-form
        class="timeForm"
        ref="timeFormRef"
        :form-data="formData"
        :form-rules="formRules"
        :form-config-list="timeConfigList"
        :labelPosition="'top'"
        :rowGutter="24"
        :smallAutoColumn="3"
        :bigAutoColumn="3"
        :needOperateBtn="false"
        :needBottomLine="false"
      >
        <template v-slot:timeSchedule>
          <div class="timeSchedule-title">{{ $t('BUC.taskPlan.timeSchedule') }}</div>
          <el-divider class="card-divider"></el-divider>
        </template>
        <template v-slot:scheduleType>
          <el-form-item prop="nextTimeSlotTask" :label="$t('BUC.taskPlan.scheduleType')" style="margin-top: 5px">
            <el-radio v-model="formData.nextTimeSlotTask" :disabled="status !== 'Succeeded'" label="0">{{
              $t('BUC.taskPlan.customized')
            }}</el-radio>
            <el-radio v-model="formData.nextTimeSlotTask" :disabled="status !== 'Succeeded'" label="1">{{
              $t('BUC.taskPlan.nextAvailableTimeSlot')
            }}</el-radio>
          </el-form-item>
        </template>
        <template v-slot:hourSegment v-if="isShowHourSegment">
          <el-form-item label="Hour Segment" prop="hourSegment" class="hour-segment">
            <el-radio-group v-model="formData.hourSegment" type="vertical">
              <el-radio
                v-for="(item, index) in timeSlotList"
                :key="index"
                :label="item.time"
                :disabled="item.tpsLimit >= 100"
              >
                {{ item.label }}<el-progress class="process-item" :percentage="item.tpsLimit"></el-progress>
              </el-radio>
            </el-radio-group>
          </el-form-item>
        </template>
      </portal-form>
      <footer-tap
        @reset="cancel"
        @submit="submit"
        :submit-loading="submitLoading"
        :showCancel="true"
        :hasSidebar="true"
      />
    </div>
    <portal-create-success
      v-else
      :successText="isWorkFlowOpen ? $t('BUC.taskPlan.successText') : $t('BUC.taskPlan.successText2')"
      :detailText="$t('IDC.till.viewDetail') + '>>'"
      :addText="$t('common.button.continueAdd') + '>>'"
      @view-detail="viewDetail"
      @continue-add="continueToAdd"
    />
    <portal-dialog
      :visible.sync="dialogVisible"
      :title="title"
      :cancelText="$t('common.button.close')"
      :width="'800px'"
      :height="'442px'"
      @cancel-btn-click="handleClose"
      :before-close="handleClose"
      :showSubmit="false"
      :showCancel="false"
    >
      <template #content>
        <table-page
          ref="executionInfoTable"
          size="small"
          :has-pagination="true"
          :columns="dialogColumns"
          :pagination="dialogPaginationConfig"
          :getTableData="dialogGetTableData"
          :is-search="refreshTableFlag"
        >
        </table-page>
      </template>
    </portal-dialog>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import PortalForm from '@/components/portal-form';
import PortalDialog from '@/components/portal-dialog';
import TablePage from '@/components/tablePage';
import PortalUpload from '@/components/portal-upload';
import PortalCreateSuccess from '@/components/portal-create-success';
import { getCreateTaskPlanRules } from '../../../validator/bulk-task-plan.js';
import footerTap from '@/components/portal-footer-tap';
import {
  preValidation,
  queryBulkDataDicts,
  queryBulkTypes,
  addTaskPlan,
  queryTimeSlot,
  queryValidRecords,
  queryPreValidationResult,
  getServiceMapping,
  queryProduct,
  queryService,
  queryReasonType,
} from '../../../api/api-bulk';
import {
  getOrganizationDetail,
  queryChildren,
  querySubscribeProducts,
} from '@/views/identifier-center/api/api-organization';
import { mapGetters } from 'vuex';
import FileUtil from '@/common/utils/file-utils';
import TopNavigation from '../components/top-navigation.vue';
import { IDENTITY_STATUS_TYPE } from '@/views/identifier-center/pages/organization/utils/organizatoin-enums';
import { dealApiRes } from '@/views/identifier-center/pages/organization/utils/common';
export default {
  name: 'CreateTaskPlan',
  data() {
    return {
      isWorkFlowOpen: false,
      bulkTypeId: '',
      bulkTypeName: '',
      titleConfig: {
        title: this.$t('BUC.taskPlan.createPageTitle'),
        desc: this.$t('BUC.taskPlan.createPageDesc'),
      },
      timeSlotList: [],
      planID: '',
      formData: {
        description: '',
        bulkFileID: '',
        bulkTypeID: '',
        planName: '',
        bulkType: '',
        fileFormat: '',
        fileName: '',
        fileContent: '',
        nextTimeSlotTask: '0',
        scheduleTime: '',
        hourSegment: '',
        orgIdentityId: '',
        service: '',
        reasonType: '',
        currency: '',
        commentToCustomer: '',
      },
      dialogVisible: false,
      dialogOperMode: '',
      refreshTableFlag: false,
      bulkTypeOptions: [],
      submitLoading: false,
      createCompleted: false,
      fileReader: '',
      isUpload: false,
      cancelled: false,
      status: 'NoUploaded',
      fullscreenLoading: false,
      validResult: {
        fileStatus: '',
        totalCount: '',
        validCount: '',
        errorCount: '',
      },
      dialogPaginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 5,
        layout: 'total, prev, pager, next',
      },

      timerInterval: '3000',
      intervalId: '',
      bulkTypeSupportFileFormat: 'XML',
      organizationList: [],
      serviceList: [],
      reasontypeList: [],
      currencyList: [],
      isShowService: true,
      isShowCurrency: true,
      isShowReasonType: true,
      productId: [],
      selectedServiceAlias: '',
      isOperationForm: false,
    };
  },
  components: {
    PortalHeader,
    PortalForm,
    PortalUpload,
    PortalDialog,
    TablePage,
    PortalCreateSuccess,
    TopNavigation,
    footerTap,
  },
  watch: {
    'formData.nextTimeSlotTask': {
      handler() {
        this.formData.scheduleTime = '';
        this.formData.hourSegment = '';
        this.timeSlotList = [];
      },
    },
    'formData.scheduleTime': {
      handler() {
        this.formData.bulkType && this.formData.scheduleTime && this.queryTimeSlot();
      },
    },
    status: {
      handler() {
        this.formData.nextTimeSlotTask = '0';
      },
    },
    formData: {
      handler() {
        this.isOperationForm = true;
      },
      deep: true,
    },
  },
  computed: {
    title() {
      switch (this.dialogOperMode) {
        case 'valid_success':
          return 'Effective Items Detail';
        case 'valid_failed':
          return 'Error Item Detail';
        default:
          return '';
      }
    },
    isShowHourSegment() {
      let isShowHourSegment =
        this.timeSlotList !== null &&
        this.timeSlotList.length > 0 &&
        this.formData.scheduleTime &&
        this.status === 'Succeeded';
      return isShowHourSegment;
    },
    dialogColumns() {
      switch (this.dialogOperMode) {
        case 'valid_success':
          return [
            { label: `Number`, prop: 'lineNo', align: 'left' },
            { label: `Summary`, prop: 'summary', align: 'left' },
          ];
        case 'valid_failed':
          return [
            { label: `Number`, prop: 'lineNo', align: 'left' },
            { label: `Summary`, prop: 'summary', align: 'left' },
            { label: `Error Message`, prop: 'errorDesc', align: 'left' },
            { label: `Cause`, prop: 'errorCause', align: 'left' },
            { label: `Solution`, prop: 'errorSolution', align: 'left' },
          ];
        default:
          return [];
      }
    },

    /**
     *@param validLevel
     * 校验级别,多个用逗号隔开
     * ERROR("0", "Error"),
     * INVALID("1", "Invalid"), 无效:数据不合法
     * NORMAL("2", "Normal"); // 正常
     */
    dialogGetTableData() {
      switch (this.dialogOperMode) {
        case 'valid_success':
          return this.getRecordsTableData(2);
        case 'valid_failed':
          return this.getRecordsTableData(0);
        default:
          return null;
      }
    },
    acceptFileFormat() {
      return this.formData.fileFormat;
    },
    fileUploadDisabled() {
      return (this.acceptFileFormat || '').length <= 0;
    },
    supportFileFormat() {
      return this.$t('common.tips.uploadFileFormat', { obj: this.acceptFileFormat });
    },
    formRules() {
      return getCreateTaskPlanRules();
    },
    timeConfigList() {
      return [
        {
          bigSpan: 3,
          smallSpan: 3,
          key: 'timeSchedule',
          slotName: 'timeSchedule',
        },
        {
          key: 'scheduleType',
          slotName: 'scheduleType',
        },
        {
          hidden: this.formData.nextTimeSlotTask !== '0' || this.status !== 'Succeeded',
          formItemClass: 'form-special-date-box',
          label: this.$t('BUC.taskPlan.date'),
          prop: 'scheduleTime',
          key: 'scheduleTime',
          comp: 'el-date-picker',
          bind: {
            align: 'right',
            type: 'date',
            format: 'dd-MM-yyyy',
            'value-format': 'yyyyMMdd',
            'prefix-icon': 'el-icon-date data_icon',
            'picker-options': {
              disabledDate(time) {
                return time.getTime() < Date.now() - 8.64e7;
              },
            },
            placeholder: this.$t('common.placeholder.select'),
          },
        },
        {
          bigSpan: 3,
          smallSpan: 3,
          key: 'hourSegment',
          slotName: 'hourSegment',
        },
      ];
    },
    formConfigList() {
      return [
        {
          bigSpan: 3,
          smallSpan: 3,
          key: 'basicInfoTitle',
          slotName: 'basicInfoTitle',
        },
        {
          formItemClass: 'form-more-select-box',
          label: this.$t('BUC.taskPlan.planName'),
          key: 'planName',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: this.$t('BUC.taskPlan.bulkTypeName'),
          key: 'bulkType',
          prop: 'bulkType',
          comp: 'el-select',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            clearable: true,
          },
          options: this.bulkTypeOptions,
          event: {
            change: (id) => {
              this.bulkTypeChange();
              let target = this.bulkTypeOptions.find((item) => item.id === id);
              if (!target) {
                target = '';
              }
              this.bulkTypeId = target.id;
              this.bulkTypeSupportFileFormat = target.fileFormat || '';
              this.formData.fileFormat = target.fileFormat || '';
              if (this.supportOrgBulkType.includes(this.bulkTypeId)) {
                this.getOrganizationList();
              }
            },
          },
        },
        {
          label: this.$t('BUC.taskPlan.fileTemplate'),
          key: 'fileFormat',
          comp: 'el-select',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            disabled: true,
          },
          options: this.availabelBulkFileFormatOptions,
        },
        {
          label: this.$t('BUC.taskPlan.file'),
          key: 'upload',
          slotName: 'upload',
        },
        {
          label: this.$t('common.label.description'),
          key: 'description',
          comp: 'el-input',
          bigSpan: 3,
          smallSpan: 3,
          bind: {
            type: 'textarea',
            rows: 4,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          hidden: !this.supportOrgBulkType.includes(this.bulkTypeId),
          label: this.$t('BUC.taskPlan.organizationName'),
          key: 'orgIdentityId',
          id: 'dialog_initiatorType',
          comp: 'el-select',
          bind: {
            placeholder: 'Please enter',
            id: 'dialog_initiatorType',
            clearable: true,
          },
          event: {
            change: (val) => {
              this.selectedServiceAlias = '';
              if (val) {
                this.isShowService = false;
                this.getServiceList(val);
              } else {
                this.isShowService = true;
                this.formData.service = '';
                this.formData.reasonType = '';
                this.formData.currency = '';
              }
            },
          },
          options: this.organizationList,
        },
        {
          hidden: this.isShowService,
          label: this.$t('BUC.taskPlan.service'),
          key: 'service',
          comp: 'el-select',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            clearable: true,
          },
          options: this.serviceList,
          event: {
            change: (val) => {
              if (val) {
                this.currencyList = [];
                let serviceList = this.serviceList;
                serviceList.forEach((item) => {
                  if (val === item.value) {
                    this.selectedServiceAlias = item.alias;
                    if (item.key === 'AUTOMATIC') {
                      this.isShowReasonType = true;
                      this.isShowCurrency = true;
                    } else {
                      this.isShowReasonType = false;
                      this.isShowCurrency = false;
                    }
                  }
                });
              } else {
                this.isShowReasonType = true;
                this.formData.reasonType = '';
                this.formData.currency = '';
              }
            },
          },
        },
        {
          hidden: this.isShowReasonType,
          label: this.$t('BUC.taskPlan.resonType'),
          key: 'reasonType',
          comp: 'el-select',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            clearable: true,
          },
          options: this.reasontypeList,
          event: {
            change: (val) => {
              if (val) {
                this.currencyList = [];
                let reasontypeList = this.reasontypeList;
                reasontypeList.forEach((item) => {
                  if (val === item.value) {
                    item.currency.forEach((item) => {
                      this.currencyList.push({
                        value: item.reasonIndex,
                        label: item.currencyCode,
                      });
                    });
                  }
                });
                this.isShowCurrency = false;
              } else {
                this.formData.currency = '';
                this.isShowCurrency = true;
              }
            },
          },
        },
        {
          hidden: this.isShowCurrency,
          label: this.$t('BUC.taskPlan.currency'),
          key: 'currency',
          comp: 'el-select',
          bind: {
            placeholder: this.$t('common.placeholder.select'),
            clearable: true,
          },
          options: this.currencyList,
        },
        {
          hidden: !this.supportOrgBulkType.includes(this.bulkTypeId),
          label: this.$t('BUC.taskPlan.commentToCustomer'),
          key: 'commentToCustomer',
          comp: 'el-input',
          bigSpan: 3,
          smallSpan: 3,
          bind: {
            type: 'textarea',
            rows: 4,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
      ];
    },
    ...mapGetters('datadict', ['moduleDict']),
    bulkFileFormatOptions() {
      return this.moduleDict.bulk['config.bulk.file-format.list'].listWithAll || [];
    },
    bulkOrgFormatOptions() {
      return this.moduleDict.bulk['config.bulk.support-org-bulk-type.list'].listWithAll || [];
    },
    supportOrgBulkType() {
      return this.bulkOrgFormatOptions.map((item) => item.value * 1).filter((item) => item);
    },
    availabelBulkFileFormatOptions() {
      return this.bulkFileFormatOptions.filter((item) => item.value == this.bulkTypeSupportFileFormat);
    },
  },
  methods: {
    // bulk type下拉框值改变
    bulkTypeChange() {
      this.$refs['uploadRef'].deleteFile();
      this.removeFile();
      this.formData.orgIdentityId = '';
      this.formData.service = '';
      this.formData.reasonType = '';
      this.formData.currency = '';
      this.formData.commentToCustomer = '';
      this.isShowService = true;
    },
    // 下载模板
    downloadTemplate() {
      const url = `orgportal/fintech/v1/bulk/task/template/${this.bulkTypeId}/download`;
      FileUtil.downLoadFile(url, {}, 'GET');
    },
    // 获取organizationName下拉列表
    async getOrganizationList() {
      let res = await getOrganizationDetail();
      let organizationList = [];
      if (res.header && res.header.success) {
        this.orgIdentityId = res.organizationInfo.organizationId;
        organizationList.push({
          value: res.organizationInfo.organizationId,
          shortCode: res.organizationInfo.shortCode,
          label: res.organizationInfo.organizationName,
        });
        const params = {
          organizationId: this.orgIdentityId,
          isAllChildren: true,
          status: IDENTITY_STATUS_TYPE.ACTIVE,
        };
        const data = await dealApiRes(queryChildren, 'list', params);
        for (let i = 0; i < data.length; i++) {
          organizationList.push({
            value: data[i].organizationId,
            shortCode: data[i].shortCode,
            label: data[i].organizationName,
          });
        }
        this.organizationList = organizationList;
      }
    },
    // 获取service下拉列表
    async getServiceList(val) {
      this.serviceList = [];
      let products = await dealApiRes(querySubscribeProducts, 'list', { identityId: val });
      let productIds = [];
      for (let i = 0; i < products.length; i++) {
        productIds.push(products[i].productId);
      }
      let serviceList = [];
      let serviceIdList = [];
      let queryProductRes = await queryProduct({ productIds: productIds });
      if (queryProductRes.header && queryProductRes.header.success) {
        for (let item in queryProductRes.productMaps) {
          queryProductRes.productMaps[item].productServices.forEach((data) => {
            serviceIdList.push(data.serviceIndex);
          });
        }
      }
      let { bulkTypeName } = this.bulkTypeOptions.find((item) => item.id === this.bulkTypeId);
      let params = { bulkType: bulkTypeName };
      let serviceMappingRes = await getServiceMapping(params);
      let selectServiceIds = [];
      if (
        serviceMappingRes.header &&
        serviceMappingRes.header.success &&
        serviceMappingRes.data &&
        serviceMappingRes.data.length > 0
      ) {
        let serviceIds = serviceMappingRes.data[0].serviceIds;
        // 取 bulkType和组织的service交集
        selectServiceIds = serviceIdList.filter((item) => {
          return serviceIds.includes(item);
        });
        selectServiceIds = [...new Set(selectServiceIds)];
      }
      // 如果交集为空则不请求接口
      if (selectServiceIds.length < 1) {
        return;
      }
      let queryServiceRes = await queryService({
        serviceIds: selectServiceIds,
      });
      let reasonTypeList = [];
      let reasonIdList = [];
      if (queryServiceRes.header && queryServiceRes.header.success) {
        for (let item in queryServiceRes.serviceMaps) {
          serviceList.push({
            value: queryServiceRes.serviceMaps[item].basicInfo.content.id,
            label: queryServiceRes.serviceMaps[item].basicInfo.content.alias,
            key: queryServiceRes.serviceMaps[item].basicInfo.selectMode,
            alias: queryServiceRes.serviceMaps[item].basicInfo.content.alias,
          });
          queryServiceRes.serviceMaps[item].serviceReasons.forEach((data) => {
            reasonIdList.push(data.reasonTypeId);
          });
        }
      }
      this.serviceList = serviceList;
      let reasonType = await queryReasonType({ reasonTypeIds: reasonIdList });
      for (let item in reasonType.reasonTypesMap) {
        reasonTypeList.push({
          value: reasonType.reasonTypesMap[item].reasonBasicInfo.reasonContent.id,
          label: reasonType.reasonTypesMap[item].reasonBasicInfo.reasonContent.name,
          currency: reasonType.reasonTypesMap[item].reasonCurrencies,
        });
      }
      this.reasontypeList = reasonTypeList;
    },
    // 获取currency下拉列表
    handleClose() {
      this.dialogVisible = false;
      this.dialogPaginationConfig = {
        total: 0,
        pageIndex: 1,
        pageSize: 5,
      };
    },
    viewDetailDialog(dialogOperMode) {
      this.dialogVisible = true;
      this.dialogOperMode = dialogOperMode;
      this.refreshTableFlag = true;
    },

    /**
     * 文件上传
     */
    uploadFun(file) {
      if (file.name.includes())
        if (!this.isUpload) {
          return;
        }
      if (!this.acceptFileFormat) {
        this.$message.error(this.$t('common.tips.pleaseCompletePreSteps'));
        this.status = 'Failed';
      }

      if (!file) {
        this.$message.error(this.$t('bulk.common.uploadFileTip'));
        this.status = 'Failed';
        return;
      }
      const fileType = file.name.split('.').pop();
      let filename = file.name;
      let formData = new FormData();
      formData.append('originalFileName', filename);
      formData.append('fileSignature', fileType);
      formData.append('bulkTypeID', this.formData.bulkType);
      formData.append('file', file.raw);
      this.fullscreenLoading = true;
      // 上传文件
      preValidation(formData)
        .then((res) => {
          this.validResult.fileStatus = 0;
          if (res.header && res.header.success) {
            this.formData.bulkFileID = res.data.fileID;
            this.formData.fileContent = '111';
            this.validResult.fileID = res.data.fileID;
            this.validResult.totalCount = res.data.totalCount;
            this.validResult.validCount = res.data.validCount;
            this.validResult.errorCount = res.data.errorCount;
            // 未上传完成，需要启动定时器刷新结果
            if (res.fileStatus != '1') {
              this.refreshUploadResult();
            }
          } else {
            this.fullscreenLoading = false;
            this.$refs['uploadRef'].deleteMemoryFile();
            this.status = 'Failed';
          }
        })
        .catch(() => {
          this.fullscreenLoading = false;
          this.$refs['uploadRef'].deleteMemoryFile();
          this.status = 'Failed';
        });
    },

    /**
     * 刷新上传结果
     */
    refreshUploadResult() {
      this.intervalId = setInterval(() => {
        this.queryUploadResult(this.formData.bulkFileID);
      }, this.timerInterval);
    },

    /**
     * 查询预校验结果
     *
     * @param fileID
     */
    queryUploadResult(fileID) {
      queryPreValidationResult(fileID).then((res) => {
        if (res.header && res.header.success) {
          this.validResult.fileID = res.data ? res.data.fileID : '';
          this.validResult.fileStatus = res.data ? res.data.fileStatus : '';
          this.validResult.totalCount = res.data ? res.data.totalCount : '';
          this.validResult.validCount = res.data ? res.data.validCount : '';
          this.validResult.errorCount = res.data ? res.data.errorCount : '';
          if (res.data && res.data.fileStatus == '1') {
            // fileStatus为1表示文件解析完成, 需要展示解析结果
            this.preValidationSuccess();
          } else if (res.data && res.data.fileStatus == '2') {
            // 预校验失败,停止刷新
            this.preValidationFailed();
          }
        } else {
          // 预校验失败(抛异常场景)
          this.preValidationFailed();
        }
      });
    },

    /**
     * 预校验成功
     */
    preValidationSuccess() {
      // 展示预校验结果
      this.displayUploadResult = true;

      this.status = 'Succeeded';
      // 停止页面加载
      this.fullscreenLoading = false;
      // 停止刷新
      clearInterval(this.intervalId);
    },

    /**
     * 预校验失败
     */
    preValidationFailed() {
      // 停止页面加载
      this.fullscreenLoading = false;

      this.status = 'Failed';
      this.$refs['uploadRef'].deleteMemoryFile();

      // 停止刷新
      clearInterval(this.intervalId);
      // 失败提示
      this.$confirm('Failed to parse the file.', 'Error', {
        confirmButtonText: 'Close',
        showCancelButton: false,
        type: 'error',
      }).then(() => {});
    },

    isUploadFun(uploaded) {
      this.isUpload = uploaded;
    },
    isCancleFun(cancelled) {
      this.cancelled = cancelled;
    },

    /**
     * 查询时间槽
     */
    queryTimeSlot() {
      let { serviceCode } = this.bulkTypeOptions.find((item) => item.id === this.formData.bulkType);
      let param = {
        serviceCode,
        tpsDate: this.formData.scheduleTime,
      };
      queryTimeSlot(param).then((res) => {
        if (res.header && res.header.success) {
          let bulkTimeSlotList = res.data.bulkTimeSlotList;
          let timeSlotList = [];
          if (bulkTimeSlotList) {
            let timeSlot = bulkTimeSlotList[0];
            for (let i = 1; i <= 24; i++) {
              this.addItem(timeSlotList, i, timeSlot['timeSlot' + i]);
            }
          }
          this.timeSlotList = timeSlotList;
          // 数据刷新需要清空上一次选择的时间
          this.formData.hourSegment = '';
        }
      });
    },

    getTimeSlotLabel(index) {
      let str1 = index - 1 < 10 ? '0' + String(index - 1) : index - 1;
      let str2 = index < 10 ? '0' + String(index) : index;
      return str1 + ':00-' + str2 + ':00';
    },

    addItem(timeSlotArr, index, tpsLimit) {
      // -1 表示当前时间之前的时间槽，任务不能执行所以不能选择
      if (tpsLimit && tpsLimit != -1) {
        let str1 = index - 1 < 10 ? '0' + String(index - 1) : index - 1;
        let timeSlotLabel = this.getTimeSlotLabel(index);
        timeSlotArr.push({ time: str1 + '0000', label: timeSlotLabel, tpsLimit: Number(tpsLimit) });
      }
    },
    viewDetail() {
      let params = {
        bulkPlanID: this.planID,
        mode: 'taskPlan',
        type: 'process',
      };
      this.$router.push({
        path: '../../bulk/bulkTask/details',
        query: { params: encodeURIComponent(JSON.stringify(params)) },
      });
    },
    continueToAdd() {
      this.refreshCurrentPage();
    },
    submit() {
      this.$refs['taskPlanFormRef'].validate((valid) => {
        this.$refs['timeFormRef'].validate((val) => {
          if (valid && val) {
            this.$confirm('Are you sure to submit the task?', {
              confirmButtonText: 'Confirm',
              cancelButtonText: 'Cancel',
              closeOnClickModal: false,
              type: 'warning',
            }).then(() => {
              this.submitLoading = true;
              this.formData.bulkTypeID = this.formData.bulkType;
              let { serviceCode } = this.bulkTypeOptions.find((item) => item.id === this.formData.bulkTypeID);
              let {
                orgIdentityId,
                reasonType,
                bulkTypeID,
                planName,
                description,
                fileFormat,
                nextTimeSlotTask,
                bulkFileID,
                currency,
                commentToCustomer,
              } = this.formData;
              let orgShortCode = '';
              for (let i = 0; i < this.organizationList.length; i++) {
                let org = this.organizationList[i];
                if (org.value == orgIdentityId) {
                  orgShortCode = org.shortCode;
                }
              }
              let parameter = {
                serviceCode: this.selectedServiceAlias,
                orgIdentityId: orgIdentityId,
                orgShortCode: orgShortCode,
                commentToCustomer: commentToCustomer,
                currency: currency,
                reasonType: reasonType,
              };
              let params = {
                bulkTypeID: bulkTypeID,
                planName: planName,
                fileFormat: fileFormat,
                nextTimeSlotTask: nextTimeSlotTask,
                scheduleTime: this.formData.scheduleTime + this.formData.hourSegment,
                description: description,
                parameter: JSON.stringify(parameter),
                bulkFileID: bulkFileID,
                serviceCode: serviceCode,
              };
              addTaskPlan(params).then((res) => {
                if (res.header.success) {
                  if (res.data.wfEnabled) {
                    this.isWorkFlowOpen = true;
                  } else {
                    this.isWorkFlowOpen = false;
                  }
                  this.createCompleted = true;
                  this.planID = res.data.planID;
                }
                this.submitLoading = false;
              });
            });
          }
        });
      });
    },
    cancel(closeDialog) {
      if (!closeDialog) {
        this.$confirm('Are you sure to cancel the operation?', {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancel',
          closeOnClickModal: false,
          type: 'warning',
        }).then(() => {
          this.$router.push({
            path: '../../bulk/bulkTask/list',
          });
        });
      }
    },
    removeFile() {
      this.formData.fileContent = '';
      this.formData.fileName = '';
    },
    getRecordsTableData(validLevel) {
      return () =>
        new Promise((resolve) => {
          let { pageSize, pageIndex } = this.dialogPaginationConfig;
          let paginationConfig = {
            limit: pageSize,
            offset: (pageIndex - 1) * pageSize,
            isHis: '0',
          };
          queryValidRecords({ bulkFileID: this.validResult.fileID, validLevel, ...paginationConfig })
            .then((res) => {
              if (res.header.success) {
                resolve({
                  header: res.header,
                  totalRecords: res.data.numberOfTotal,
                  list: res.data.bulkDataInfoList || [],
                });
              }
            })
            .catch(() => {
              resolve();
            })
            .finally(() => {
              this.refreshTableFlag = false;
            });
        });
    },
    async initDataDict() {
      this.$store.dispatch('datadict/getModuleDicts', {
        moduleName: 'bulk',
        dictCodes: ['config.bulk.file-format.list', 'config.bulk.support-org-bulk-type.list'],
        queryAPI: queryBulkDataDicts,
      });
    },
    async initBulkTypes() {
      await queryBulkTypes({ limit: 50, offset: 0 }).then((res) => {
        this.bulkTypeOptions = (res.data || []).map((type) => {
          return {
            ...type,
            value: type.id,
            label: type.bulkTypeName,
          };
        });
      });
    },
  },
  mounted() {
    if (!window.FileReader) {
      this.$message.error(this.$t('common.tips.notSupportFileReader'));
    }
    this.fileReader = new FileReader();
  },
  created() {
    this.initBulkTypes();
    this.initDataDict();
  },
};
</script>

