<template>
  <el-upload
    class="upload"
    drag
    :limit="fileCountLimit"
    ref="upload"
    :action="uploadActionUrl"
    :http-request="uploadFun"
    multiple
    :accept="accept"
    :on-change="onChangeHandle"
    :show-file-list="false"
    :file-list="fileList"
    style="display: inline-block float:right"
    :auto-upload="autoUpload"
    :disabled="disabled"
    :class="{ 'cursor-disabled': disabled }"
  >
    <el-button
      @click="clickUpload()"
      class="upload-text"
      :class="{ disabled: disabled }"
      v-if="status === 'NoUploaded'"
      type="text"
      size="small"
      plain
    >
      <svg-icon icon-class="file-upload" v-if="!disabled" style="width: 16px; height: 16px; stroke-width: 0" />
      <svg-icon icon-class="file-upload-disabled" v-else style="width: 16px; height: 16px; stroke-width: 0" />
      <span style="padding-left: 8px">Upload</span>
    </el-button>
    <div class="uploaded" v-if="status !== 'NoUploaded'">
      <!-- 下载中 -->
      <div class="progress" v-if="status === 'Uploading'">
        <div class="content">
          <div class="tex">
            <span>Uploading File...{{ loadProgress }}%</span>
            <el-button @click.stop="closeUploading()" class="icon-delete" icon="el-icon-close" type="text"></el-button>
          </div>
          <el-progress class="bar" v-if="progressFlag" :percentage="loadProgress" :format="format"></el-progress>
        </div>
      </div>
      <!-- 下载成功 -->
      <div class="success" v-if="status === 'Succeeded'">
        <div class="content">
          <div class="content-item1"><i class="el-icon-a-uploadingFile"></i></div>
          <div class="content-item2">
            <span class="item2-title" :title="file.name">{{ file.name }}</span>
            <div class="item2-con">
              <span class="span1"> {{ $t('BUC.taskPlan.totalItems') + '   ' + totalItem.totalCount }}</span>
              <span class="span2" @click.stop="uploadedItems">
                {{ $t('BUC.taskPlan.uploadedItems') + '   ' + totalItem.uploadedItems }}</span
              >
              <span :class="totalItem.errorItems != '0' ? 'span3' : 'span2'" @click.stop="errorItems">
                {{ $t('BUC.taskPlan.errorItems') + '   ' + totalItem.errorItems }}</span
              >
            </div>
          </div>
        </div>
      </div>
      <div class="sucess-btn" v-if="status === 'Succeeded'">
        <el-button @click.stop="deleteFile()" icon="el-icon-delete" type="text" style="font-size: 16px"></el-button>
      </div>
      <div class="sucess-btn" v-if="status === 'Failed'">
        <svg-icon icon-class="file-upload" style="width: 16px; height: 16px; stroke-width: 0" />
      </div>
      <!-- 下载失败 -->
      <div class="failed" v-if="status === 'Failed'">
        <div class="content">
          <div class="failed-content"><i class="el-icon-a-uploadingFile"></i></div>
          <div class="failed-content-item2">
            <div class="name" :title="file.name">{{ file.name }}</div>
            <div class="status">Upload failed</div>
          </div>
        </div>
      </div>
      <div class="failed-btn" v-if="status === 'Failed'">
        <el-button icon="el-icon-upload2" type="text" style="font-size: 16px"></el-button>
      </div>
    </div>
  </el-upload>
</template>

<script>
export default {
  name: 'PortalUpload',
  props: {
    uploadActionUrl: {
      type: String,
      default: '',
    },
    status: {
      type: String,
      default: 'NoUploaded',
    },
    uploadData: {
      default: null,
    },
    accept: {
      type: String,
      default: null,
    },
    uploadFun: {
      type: Function,
    },
    disabled: {
      type: Boolean,
      default: true,
    },
    clickUpload: {
      type: Function,
      default: () => {},
    },
    autoUpload: {
      type: Boolean,
      default: true,
    },
    fileWhiteList: {
      type: Array,
      default: () => [],
    },
    fileSizeLimit: {
      type: Number,
      default: () => 1024 * 1024 * 10,
    },
    fileCountLimit: {
      type: Number,
      default: () => 1,
    },
    errorFileFormatMsg: {
      type: String,
      default: () => 'common.tips.errorFileFormatMsg',
    },
    fileExceedSizeMsg: {
      type: String,
      default: () => 'common.tips.fileExceedSize',
    },
    totalItem: {
      type: Object,
      default: () => {
        return {
          totalCount: 0,
          uploadedItems: 0,
          errorItems: 0,
        };
      },
    },
  },
  data() {
    return {
      loadProgress: 0,
      progressFlag: true,
      fileList: [],
      file: '',
      fileSize: '',
      isCancel: false,
    };
  },
  methods: {
    formatSize(size) {
      let sizeStr = '';
      if (size / 1024 / 1024 > 1) {
        sizeStr = (size / 1024 / 1024).toFixed(2) + 'MB';
      } else if (size / 1024 > 1) {
        sizeStr = (size / 1024).toFixed(2) + 'KB';
      } else {
        sizeStr = size.toFixed(2) + 'B';
      }
      return sizeStr;
    },
    // 文件状态改变执行
    onChangeHandle(file, fileList) {
      this.fileList = fileList;
      const fileSuffix = file.name.substring(file.name.lastIndexOf('.') + 1);
      const isSuffixInvalid =
        this.fileWhiteList.map((item) => item.toLowerCase()).indexOf(fileSuffix.toLowerCase()) === -1;
      if (isSuffixInvalid) {
        this.$message.error(this.$t(this.errorFileFormatMsg, { format: this.fileWhiteList }));
        const currIdx = this.fileList.indexOf(file);
        this.fileList.splice(currIdx, 1);
        this.$emit('isUploadFun', false);
        return;
      }
      const isSizeExceed = file.size / this.fileSizeLimit > 1;
      if (isSizeExceed) {
        this.$message.error(this.$t(this.fileExceedSizeMsg, { limit: this.formatSize(this.fileSizeLimit) }));
        const currIdx = this.fileList.indexOf(file);
        this.fileList.splice(currIdx, 1);
        this.$emit('isUploadFun', false);
        return;
      }
      this.file = file;
      this.fileSize = this.formatSize(file.size);
      if (file.status === 'ready') {
        this.$emit('isUploadFun', true);
        this.$refs.upload.clearFiles();
        this.$emit('update:status', 'Uploading');
        this.loadProgressFun();
      }
      if (!this.autoUpload) {
        this.uploadFun(file);
      }
    },
    loadProgressFun() {
      this.progressFlag = true;
      let time = 0;
      this.loadProgress = 0;
      this.isCancel = false;
      let interval = setInterval(() => {
        this.loadProgress += 1;
        time += 1;
        if (time === 98) {
          this.progressFlag = false;
          time = 0;
          this.loadProgress = 98;
          clearTimeout(interval);
        }
        if (this.isCancel) {
          this.progressFlag = false;
          time = 0;
          this.loadProgress = 98;
          clearTimeout(interval);
        }
      }, 500);
    },
    // 取消组件自带半分比样式
    format(percentage) {
      if (percentage < 100 && percentage > 0) {
        return ' ';
      }
    },
    // 删除已上传的文件
    deleteFile() {
      this.deleteMemoryFile();
      this.$emit('update:status', 'NoUploaded');
      this.$emit('removeFile');
    },
    // 清空缓存文件
    deleteMemoryFile() {
      this.$refs.upload.clearFiles();
    },
    // 取消上传
    closeUploading() {
      this.$emit('isCancleFun', true);
      this.isCancel = true;
      this.deleteMemoryFile();
      this.$refs.upload.abort();
      this.$emit('update:status', 'NoUploaded');
    },
    uploadedItems() {
      this.$emit('viewDetailDialog', 'valid_success');
    },
    errorItems() {
      this.$emit('viewDetailDialog', 'valid_failed');
    },
  },
};
</script>

